.spin-hand {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 21px;
    height: 21px;
}

.halo-container {
    position: relative;
    width: 100%;
    height: 100%;
}

.halo-img {
    position: absolute;
    top: -9px;
    left: 4px;
    width: 220%;
    height: 220%;
    animation: rotate360 5s linear infinite;
    /* Rotate halo infinitely */
}

.point-hand-img {
    position: absolute;
    top: 129%;
    left: 183%;
    transform: translate(-50%, -50%);
    width: 50px;
    height: 50px;
    cursor: pointer;
    animation: handclick 4.5s linear infinite;
}


@keyframes handclick {
    0% {
        transform: translate(-50%, -50%) scale(0.7);
    }
    50% {
        transform: translate(-50%, -50%) scale(1);
    }
    
    100% {
        transform: translate(-50%, -50%) scale(0.7);
    }
  }







/* ******************************************* channel join popup ***************************
******************************************* channel join popup ***************************
******************************************* channel join popup ***************************
******************************************* channel join popup *************************** */

.popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 95%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    text-align: center;
    gap: 5px;
}

.popup-content {
    width: 75%;
    max-width: 600px;
    background-color: rgb(140, 32, 179);
    padding: 20px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    border: 2px solid rgba(255, 255, 255, .4);
}

.popup-img {
    width: 150px;
    height: auto;
}

.popup-close {
    position: absolute;
    top: 10px;
    right: 10px;
    background: transparent;
    border: none;
    font-size: 18px;
    cursor: pointer;
}


.join-btn {
    padding: 5px;
    border-radius: 5px;
    font-size: 18px;
    font-weight: 600;
    width: 90%;
    margin: 11px 0px;
    cursor: pointer;
    font-style: italic;
    border: none;
}




