*{
    margin: 0px;
    padding: 0px;
   
}

.admin-pannal{
    background-color: #fff;
    box-sizing: border-box;
    height: 100vh;
    width: 100%;
    
  
}
.login-face{
    width: 70%;
    height: auto;
    background-color: rgb(168, 94, 238);
    border-radius: 12px;
    gap: 15px;
    padding: 12px;
}
.login-fild{
padding: 8px 5px;
    width: 100%;
    margin: 10px;
}
.login-fild label{
padding: 8px 5px;
    width: 20%;
    margin: 10px;
    font-weight: bolder;
}
.login-fild input{
padding: 10px;
    width: 40%;
    margin: 10px;
    font-weight: bolder;
    border-radius: 12px;
    border: none;
   outline: none;
}
.input-btn{
    width: 100%;
}
.btn-ad{
    background-color: rgb(82, 22, 179);
    /* width: 600%; */
    font-size: 18px ;
    padding: 10px 20px;
}